html,
body,
#root {
/*
border: solid;
border-width: 2px;
border-color: gray;
*/
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  /* Prevent text selection of <div> element. */
  user-select: none;
  background: url("./assets/gray-blur.jpg") no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-size: 100%;
  white-space: pre-wrap;
}

h1, h2, h3, h4 {
  margin: 0px;
  line-height: 25px;
  font-weight: 900;
  text-transform: uppercase;
}

p {
  font-size: 0.9em;
  line-height: 18px;
}

ul, li {
  padding: 4px;
  margin: 0;
  list-style-type: none;
}

a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
  color: rgba(255, 255, 255, .6);
}

/************************************ Main ************************************/
.container {
/*
border: solid;
border-width: 2px;
border-color: red;
*/
  /*overflow: hidden;*/
  position: absolute;
  display: grid;
  grid-template: 9vh auto 28vh / auto auto 50vh;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
}

/*********************************** Title ************************************/
.title-container {
/*
border: solid;
border-width: 2px;
border-color: blue;
*/
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 3;
  width: 100%;
  height: 100%;
  padding: 15px;
  color: rgba(227,228,230, 0.8);
  font-size: 2.2em;
  font-weight: 900;
  font-style: italic;
  text-transform: uppercase;
  letter-spacing: -2px;
}

/*********************************** NavBar ***********************************/
.navbar-container {
/*
border: solid;
border-width: 2px;
border-color: purple;
*/
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 3;
  grid-column-end: 5;
  width: 100%;
  height: 100%;
}

