/********************************** Basecard **********************************/
.basecard-container {
/*
border: solid;
border-width: 2px;
border-color: green;
*/
  display: flex;
  justify-content: center;
  align-items: center;
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 4;
  width: 100%;
  height: 100%;
}

.basecard-main {
  padding-left: 40px;
  padding-top: 20px;
  padding-right: 25px;
  display: flex;
  border: solid;
  border-width: 3px;
  border-color: rgba(227,228,230, 0.9);
  border-radius: 10px;
  width: 60vw;
  height: 25vw;
  overflow-x: hidden; 
  overflow-y: scroll;
  /* Covers entire container. */
  background: rgba(169, 173, 182, 0.8);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  -ms-overflow-style: none;
  scrollbar-width: none;
  /*
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
  */
}

.basecard-main::-webkit-scrollbar { display: none; }

.basecard-home {
  margin: -25px;
  position: absolute;
  width: 100%;
  height: 100%;
  color: rgba(227, 228, 230, 0.8);
  letter-spacing: 2px;
  content: "\A";
  line-height: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.basecard-data-container {
  width: 100%;
  height: auto;
  margin: 0px;
  color: rgba(227, 228, 230, 0.8);
  letter-spacing: 2px;
  content: "\A";
  line-height: 1px;
}

.basecard-data-heading1 {
  text-align: right;
}

.basecard-data-content1 {
  font-weight: bold;
  text-align: right;
}

.basecard-data-content4 {
  font-family: Courier New;
}

/********************************** Carousel **********************************/
.carousel-container {
/*
border: solid;
border-width: 2px;
border-color: yellow;
*/
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 18vh;
  width: 195vh;
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 5;
}

